import { Pokemon as PokemonResponse, PokemonList } from './model/ResponseTypes';

export const buildUrl = (path: string) => `https://pokeapi.co/api/v2/${path}`;

export function getAll({ pageParam = 0 }: any): Promise<PokemonList> {
  const queryParams = [
    ['offset', pageParam],
    ['limit', 66],
  ];
  const fullUrl = new URL(buildUrl('pokemon'));
  queryParams.forEach(([key, val]) => fullUrl.searchParams.append(key, val));

  return fetch(fullUrl.toString()).then((res) => res.json());
}

export function get(url: string): Promise<PokemonResponse> {
  return fetch(url).then((res) => res.json());
}
