const exists = (val?: any) => val !== undefined && val !== null;

const OPACITY_70_PERCENT = Math.ceil(0.7 * 255);
export const hexWithOpacity = (
  originalValue: string,
  opacityLevel?: number,
) => {
  // opacity should be int > 0 and < 256
  let opacity = opacityLevel;
  if (!exists(opacityLevel)) {
    opacity = OPACITY_70_PERCENT;
  } else if (opacityLevel! % 1 > 0) {
    // opacityLevel is a float
    opacity = opacityLevel! * 255;
  }

  return originalValue + opacity!.toString(16);
};
