import React from 'react';
import { useQuery } from 'react-query';
import { Link } from 'react-router-dom';
import { get } from './service';
import {
  Pokemon as PokemonResponse,
  PokemonResource,
} from './model/ResponseTypes';
import { StatusContainer } from './UtilityComponents';
import Card from './Card';

type GridCardProps = {
  resource: PokemonResource;
  style?: any;
};

export default function GridCard({ resource, style }: GridCardProps) {
  const queryResults = useQuery<PokemonResponse, Error>(resource.url, () =>
    get(resource.url),
  );
  const statusProps = { ...queryResults };

  return (
    <StatusContainer {...statusProps}>
      {(responseData: PokemonResponse) => {
        const pokemon: PokemonResponse | undefined = responseData;
        const pokemonName = pokemon
          ? pokemon.name.replace(/^\w/, (l) => l.toUpperCase())
          : '';

        return (
          <Card>
            {(styles: any) => (
              <Link
                style={{
                  ...styles.card,
                  ...style,
                  backgroundImage: `url(${pokemon?.sprites.front_default})`,
                }}
                to={`/${pokemon?.id}`}
              >
                <article style={styles.textBar}>{pokemonName}</article>
              </Link>
            )}
          </Card>
        );
      }}
    </StatusContainer>
  );
}
