import React from 'react';
import Card from './Card';

export const BottomSpacer = () => (
  <div style={{ marginTop: 32, marginBottom: 64 }}></div>
);

export const Error = ({
  error,
  styleOverrides,
}: {
  error: Error | null;
  styleOverrides?: any;
}) => (
  <Card>
    {(styles: any) => (
      <section style={{ ...styles.card, ...styleOverrides }}>
        <article style={styles.textBar}>{error!.message}</article>
      </section>
    )}
  </Card>
);

export const Loading = ({ styleOverrides }: { styleOverrides?: any }) => (
  <Card>
    {(styles: any) => (
      <section style={{ ...styles.card, ...styleOverrides }}>
        <article style={styles.textBar}>Loading...</article>
      </section>
    )}
  </Card>
);

type StatusProps = {
  children: (data: any) => React.ReactElement;
  data: any;
  error: Error | null;
  isLoading: boolean;
  styleOverrides?: any;
};
export const StatusContainer = ({
  children,
  data,
  error,
  isLoading,
  styleOverrides,
}: StatusProps) => {
  const styles = styleOverrides || {};
  if (error) {
    return <Error error={error} styleOverrides={styles} />;
  } else if (isLoading) {
    return <Loading styleOverrides={styles} />;
  }

  return children(data);
};
