import React from 'react';
import { useTheme } from './theme';
import { Theme } from './theme/types';
import { hexWithOpacity } from './theme/utils';

const { innerWidth } = window;
const IS_MOBILE = innerWidth <= 480;

type CardProps = {
  children: (data: any) => React.ReactElement;
};

const makeStyles = (theme: Theme) => {
  const { palette: colors, spacing } = theme;
  // TODO: figure out best way to do mobile styles
  const cardSize = IS_MOBILE ? 112 : 128;

  return {
    card: {
      alignItems: 'flex-end',
      backgroundColor: colors.gray[300],
      backgroundPositionX: 'center',
      backgroundRepeat: 'no-repeat',
      border: `2px solid ${colors.gray[100]}`,
      color: colors.text.primary,
      display: 'flex',
      height: cardSize,
      imageRendering: 'pixelated',
      justifyContent: 'center',
      lineBreak: 'normal',
      margin: spacing / 2,
      textDecoration: 'none',
      width: cardSize,
    },
    textBar: {
      backgroundColor: hexWithOpacity(colors.gray[500]),
      borderTop: `2px solid ${colors.gray[750]}`,
      padding: spacing,
      width: '100%',
    },
  };
};

export default function Card({ children }: CardProps) {
  const styles = makeStyles(useTheme().state);
  return children(styles);
}
