import React from 'react';
import { useQuery } from 'react-query';
import { useParams } from 'react-router';
import { get, buildUrl } from './service';
import { Pokemon as PokemonResponse } from './model/ResponseTypes';
import { useTheme } from './theme';
import { Theme } from './theme/types';
import { StatusContainer } from './UtilityComponents';
import Card from './Card';

type ParamsType = {
  id: string;
};

const makeStyles = ({ palette: colors, spacing }: Theme) => {
  const cardSize = spacing * 32;

  return {
    detailCard: {
      backgroundSize: '100%',
      height: cardSize,
      // TODO: flex-box instead?
      margin: '0 auto',
      width: cardSize,
    },
  };
};

export default function DetailCard() {
  const { detailCard: styleOverrides } = makeStyles(useTheme().state);
  const { id } = useParams<ParamsType>();
  const resourceUrl = buildUrl(`pokemon/${id}`);
  const { isLoading, error, data } = useQuery<PokemonResponse, Error>(
    resourceUrl,
    () => get(resourceUrl),
  );
  const statusProps = { data, error, isLoading };

  return (
    <StatusContainer {...statusProps} styleOverrides={styleOverrides}>
      {(responseData: PokemonResponse) => {
        const pokemon: PokemonResponse | undefined = responseData;
        const pokemonName = pokemon
          ? pokemon.name.replace(/^\w/, (l) => l.toUpperCase())
          : '';

        return (
          <Card>
            {(styles: any) => (
              <section
                // @ts-ignore
                style={{
                  ...styles.card,
                  ...styleOverrides,
                  backgroundImage: `url(${responseData?.sprites.front_default})`,
                }}
              >
                <article style={styles.textBar}>{pokemonName}</article>
              </section>
            )}
          </Card>
        );
      }}
    </StatusContainer>
  );
}
